.experience-wrapper {
      @apply relative lg:w-10/12 mx-auto my-12 md:my-24 px-6;
}

.position {
      @apply relative flex w-full xl:w-10/12 mx-auto py-10;
}

.position span:nth-of-type(2) {
      @apply font-bold -mt-1 mb-0.5;
      
}

.position:is(:first-child) .date {
      @apply after:top-full;
}

.position:not(:first-child) .date {
      @apply after:bottom-full after:bg-copperfield;
}

.position p {
      @apply italic;
}

.position .date {
      @apply relative flex flex-col items-center justify-center border border-copperfield px-3 py-2.5;
}

.position .date::after {
      content: '';
      @apply  absolute block w-[1px] h-full  bg-copperfield;      
}

.position .title {
      @apply pl-4 md:pl-6 w-auto;
}

.sub {
      @apply flex items-center py-0 z-[1] -mt-4 sm:-mt-8 mb-4;
}


.sub::before {
      content: '';
      @apply  block w-[100px] min-w-[80px] h-[1px] mr-2 bg-copperfield ml-9;
}

.sub::after {
      content: url("data:image/svg+xml;utf8,<svg version='1.2' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 1533 1533' height='25' fill='rgb(119, 123, 144)'><path d='m766.3 0.3c-80.6 620.4-146 685.7-766.4 766.3 620.4 80.7 685.8 146 766.4 766.4 80.6-620.4 146-685.7 766.3-766.4-620.3-80.6-685.7-145.9-766.3-766.3z'/></svg>");
      fill: var(--copperfield);
      @apply absolute left-[1.2rem] top-1/2 -translate-y-1/2 mt-[.18rem] z-[2];
}



h4 {
      @apply font-marcellus text-2xl uppercase text-copperfield tracking-widest leading-tight w-auto;
      word-break: break-word;
}

p {
      @apply text-waterloo font-montserrat text-base tracking-wide leading-6 text-start w-auto;
}