.hero-sub {
  @apply my-12 mb-0 sm:flex-row scroll-mt-0;
}

.hero-sub .hero-sub-content {
  @apply w-full h-full md:w-6/12 lg:w-5/12 lg:self-baseline;
}

.hero-sub h1 {
  @apply mx-auto w-auto md:w-fit lg:pt-4 md:max-w-[750px];
}

.hero-sub h1 p {
  @apply  w-full;
}

.hero-sub h1 span {
  @apply text-4xl sm:text-7xl xl:text-8xl uppercase w-full hyphens-auto;
  word-wrap: break-word;
}

.hero-sub h1 span:first-of-type {
 @apply text-copperfield md:text-transparent z-[1];
 -webkit-text-stroke-width: 2px;
 -webkit-text-stroke-color: theme(colors.copperfield.500);
}

.hero-sub h1 span:last-of-type {
  @apply md:text-copperfield hidden md:block;
  -webkit-text-stroke-width: 0px;
 }

.hero.hero-sub figure {
  @apply relative sm:self-center xl:self-start md:w-6/12 lg:w-7/12 h-[250px] sm:h-auto -z-[0] mt-4 md:m-0 overflow-hidden;
}

.hero.hero-sub figure span {
  @apply absolute inset-0 -z-[1] bg-waterloo;
}


.hero-sub figure img {
  @apply  w-full h-full object-contain;
}

.hero-sub .info {
  @apply mt-6 sm:pr-6 lg:mt-16 xl:pr-20;
}


.hero-sub .skilllist {
  @apply w-full md:pr-12 lg:pr-16;
}