.social-wrapper {
  @apply relative w-full mt-24 mb-14 md:mb-16 md:mt-32;
}


.social-wrapper .rfm-marquee-containera {
  @apply overflow-hidden;
}

.social-wrapper .rfm-marquee-container:nth-child(even) a {
  @apply bg-copperfield text-transparent;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: theme(colors.copperfield.100);
}

.social-wrapper .rfm-marquee-container:nth-child(even):hover a {
  @apply bg-transparent text-copperfield;
  -webkit-text-stroke-width: 0;
}

.social-wrapper .rfm-marquee-container:nth-child(odd):hover a {
  @apply text-transparent;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: theme(colors.copperfield.500);
}

.social-wrapper a {
  @apply inline-block font-marcellus text-6xl sm:text-7xl md:text-8xl lg:text-8xl xl:text-9xl uppercase text-copperfield tracking-widest leading-none text-center w-min 2xl:w-full py-2 transition-all duration-150;
  transition-timing-function: cubic-bezier(.17,.67,.83,.67);
}